import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import  baseUrl  from 'src/app/_helper';

@Injectable({
  providedIn: 'root'
})
export class AdminDashboardService {

  constructor(private http:HttpClient) {}

  public getTotalEmployees(){
    return this.http.get(`${baseUrl}/api/v1/adminDashboard/getTotalEmployee`);
  }

  public getTotalEmployeesPresentToday(){
    return this.http.get(`${baseUrl}/api/v1/adminDashboard/getPresentEmployeeToday`);
  }

  public getTotalEmployeesJoinedThisMonth(){
    return this.http.get(`${baseUrl}/api/v1/adminDashboard/totalEmployeesJoinedLastMonth`);
  } 
}
